    }

    // d-day Í≥ÑÏÇ∞
    function getDday() {
      // var masTime = new Date("2022-09-24");
      const masTime = new Date("2022-09-24T13:00:00+0900");
      const todayTime = new Date();

      const diff = masTime - todayTime;

      const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));
      const diffHours = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const diffMinutes = Math.floor((diff / (1000 * 60)) % 60);
      const diffSeconds = Math.floor((diff % (1000 * 60)) / 1000);

      if (diff > 0) {
        var day = diffDay > 0 ? `${diffDay}Ïùº` : "";
        var hours = diffHours > 0 ? `${diffHours}ÏãúÍ∞Ñ` : "";
        var minutes = diffMinutes > 0 ? `${diffMinutes}Î∂Ñ` : "";
        var seconds = diffSeconds > 0 ? `${diffSeconds}Ï¥à` : "";

        console.log("interval");

        return `${day != "" ? `${day} ` : ""}
      ${hours != "" ? `${hours} ` : ""}
      ${minutes != "" ? `${minutes} ` : ""}
      ${seconds != "" ? `${seconds}` : ""}`;
      } else {
        if (ddayInterval) {
          clearInterval(ddayInterval);
        }
        return `Ï∏°ÌïòÌï¥ Ï£ºÏã† Î™®Îì† Î∂ÑÎì§ÏóêÍ≤å Í∞êÏÇ¨ÎìúÎ¶ΩÎãàÎã§.`;
      }
    }

